<template>
  <div class="bg1">
    <div class="Content">
      <div class="kong">
        <div class="kong_1" v-show="index<=3" v-for="(item,index) in list" :key="index" @click="send()">
          <p class="bishu" v-if="item.srv_OVERTIME_CN != ''">
            <span>{{ item.srv_OVERTIME_CNT }}</span> <br />
            笔超时
          </p>
          <div class="kong_1_right">
            <div class="kong_1_top">
              <p class="name" v-if="item.FAULT_SYSTEM != ''">
                {{ item.FAULT_SYSTEM }} <br />
              </p>
              <p v-if="item.START_TIME != ''" >
                <span>{{ item.START_TIME | filterC }}</span>
              </p>
            </div>
            <div class="kong_1_bottom">
              <p class="des" v-if="item.gzdw_summary != ''">
                {{ item.gzdw_summary }}
              </p>
            </div>
          </div>
        </div>
      </div>
      <div class="caozuo" style="margin-top: 0.4rem">
        <span>综合类告警</span>
        <router-link to="/now" activeClass="active" style="margin-left: 0.6rem"
          >当前告警</router-link
        >
        <router-link to="/tel" activeClass="active" style="margin-left: 0.04rem"
          >已通知告警</router-link
        >
        <router-link to="/his" activeClass="active" style="margin-left: 0.04rem"
          >历史告警</router-link
        >
      </div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list:"",
  //     list: [
  //       {
  //       srv_OVERTIME_CNT:"22",
  //       FAULT_SYSTEM:"11111111111111111111111111111111111",
  //       START_TIME:"4564748764",
  //       gzdw_summary:"哒哒哒哒哒哒多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多多多多",
  //     },
  //  {
  //       srv_OVERTIME_CNT:"22",
  //       FAULT_SYSTEM:"11111111111111111111111111111111111",
  //       START_TIME:"4562348764",
  //       gzdw_summary:"哒哒哒哒哒哒多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多多多多",
  //     },
  //  {
  //       srv_OVERTIME_CNT:"22",
  //       FAULT_SYSTEM:"11111111111111111111111111111111111",
  //       START_TIME:"0987890980",
  //       gzdw_summary:"哒哒哒哒哒哒多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多多多多",
  //     },
  //  {
  //       srv_OVERTIME_CNT:"22",
  //       FAULT_SYSTEM:"11111111111111111111111111111111111",
  //       START_TIME:"2345678764",
  //       gzdw_summary:"哒哒哒哒哒哒多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多哒哒哒哒哒哒多多多多多多多多多多多多多多",
  //     },
  //     ],
      // START_TIME: ["1234567898", "2345667898", "6756567898", "1234567898765","1234567898765"],
    };
  },
  components: {},
  created() {
    this.Init();
  },
  mounted() {
    // setInterval(() => {
    //   this.Init();
    // }, 3000);
  },
  methods: {
    Init() {
      this.$http
        // .get(this.$baseUrl+"/api/test/fault/")
        .get(this.$baseUrl + "/api/data/fault")
        .then((res) => {
          console.log(res, "res故障定位");
          console.log(res.data.data.items, "res故障定位");
          this.list = res.data.data.items;
        })
        .catch((res) => {});
    },
  send(){
    console.log("send")
  }
  },
  filters: {
    filterA(n) {
      let result = n.substr(11, 8);
      return result;
    },
    filterB(n) {
      let result = n.substr(0, 10);
      return result;
    },
    filterC(n) {
      var oDate = new Date(Number(n*1000));
      let result =
        oDate.toLocaleDateString().replace(/\//g, "-") +
        " " +
        oDate.toTimeString().substr(0, 8);
      return result;
    },
  }, 
};
</script>
<style lang="" scoped>
.bg1 {
  width: 19.2rem;
  height: 10.8rem;
  background: url(../assets/img/background.png) no-repeat;
  background-size: 100% 100%;
  position: relative;
  overflow: hidden;
}

.Content {
  width: 94%;
  height: 100%;
  margin: 0 auto;
}
.kong {
  width: 100%;
  height: 15%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.1rem;
}
.kong_1 {
  width: 20%;
  height: 100%;
  padding: 0.05rem;
  background: url(../assets/img/juxing5.png) no-repeat;
  background-size: 110% 120%;
  background-position: center;
  font-size: 0.2rem;
  color: #2ae7a5;
  display: flex;
  justify-content: space-between;
  z-index: 999;
  cursor: pointer;
}
.bishu {
  width: 15%;
  height: 100%;
  background-color: #0dcf8b;
  font-size: 0.3rem;
  text-align: center;
  color: rgba(255, 255, 255, 0.849);
  font-weight: 500;
  float: left;

}

.bishu span {
  color: #2687b4;
  font-weight: 600;
}
.kong_1_right {
  width: 70%;
  margin-right: 0.2rem;
}
.kong_1_right p {
  width: 2.5rem;
  word-wrap: break-word;
}

.kong_1_bottom {
  margin-top: 0.1rem;
  line-height: 0.24rem;
}
.des {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}
.des:hover {
  overflow: visible;
  text-overflow: inherit;
  display: block;
  background-color:  #08412C;
  padding: 0.1rem 0.2rem;
  margin: -0.1rem -0.2rem;
  border-radius: 0.1rem;
}
.name {
  font-size: 0.3rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.name:hover {
  overflow: visible;
  text-overflow: inherit;
  display: block;
}
.caozuo span {
  font-weight: 650;
  font-style: normal;
  color: #28ffc0;
  font-size: 0.32rem;
}
.caozuo a {
  color: #fff;
  font-weight: 600;
  padding: 0.16rem 0.5rem;
  border-top-right-radius: 0.3rem;
  background-color: #055a3c;
}
.active {
  color: #fff !important;
  background-color: #0ca06d !important;
  background-image: linear-gradient(#0af3a1, #185a43);
}
</style>


