import Vue from 'vue'
import Router from 'vue-router'

//引入一级路由
import Index from '@/components/index'
import More1 from '@/components/more'
import More2 from '@/components/more1'
import Fault from '@/components/fault'
import No from '@/components/no'
//引入二级路由
import Now from '@/components/pages/now'
import Tell from '@/components/pages/telled'
import History from '@/components/pages/history'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      component: Index,
      children: [
        {
          path: '/now',
          component: Now
        },
        {
          path: '/tel',
          component: Tell
        },
        {
          path: '/his',
          component: History
        },
      ],
    },
    {
      path: '/more/:table/:id',
      component: More1,
    },
    {
      path: '/more1/:table/:id',
      component: More2,
    },
    {
      path: '/fault',
      component: Fault,
    },
    {
      path: '/no',
      component: No,
    },
    {
      path: '*',
      redirect: '/now'
    },
  ]
})



