      this.$refs.duty2[index2].style.display = "none";
    },
    none() {
      event.currentTarget.parentElement.style.display = "none";
    },
    none_kong() {
      event.currentTarget.parentElement.style.display = "none";
      this.office = "";
      this.text = "";
    },
    dutyer1(index4) {
      this.$refs.notice_hover_2_a[index4].style.display = "block";
    },
    dutyer2(index4) {
      this.$refs.notice_hover_2_b[index4].style.display = "block";
    },
    Duty1(indexD) {
      console.log(this.search_dutyer1_list[indexD].StaffName, "aaaaaaaaaaaaaa");
      this.duty1.push({
        StaffName: this.search_dutyer1_list[indexD].StaffName,
        Account: this.search_dutyer1_list[indexD].Account,
      });
    },
    Duty2(indexD) {
      console.log(this.search_dutyer1_list[indexD].StaffName, "aaaaaaaaaaaaaa");
      this.duty2.push({
        StaffName: this.search_dutyer1_list[indexD].StaffName,
        Account: this.search_dutyer1_list[indexD].Account,
      });
    },
  },
  computed: {
    search_dutyer1() {
      this.$http
        .get(
          this.$baseUrl + "/api/telephone/?keyword=" + this.searchData_dutyer
        )
        .then((msg) => {
          console.log(msg, "责任人列表");
          this.search_dutyer1_list = msg.data.employes;
        })
        .catch((res) => {});
    },
  },
  watch: {
    search_dutyer1(newValue, oldValue) {
      if (this.newValue == "") {
        this.dutylist1(index2);
      }
    },
    currentTab() {
      if (this.currentTab == 0) {
        this.text = "";
      } else {
        this.office = "";
      }
    },
  },
  filters: {
    filterA(n) {
      let result = n.substr(11, 8);
      return result;
    },
    filterB(n) {
      let result = n.substr(0, 10);
      return result;
    },
  },
};
</script>

<style scoped>
@import "../../assets/css/now.css";
</style>



