  /* display: flex; */
}
/* .destroy span {
  display: inline-block;
  width: 30%;
}
.destroy i {
  display: inline-block;
  width: 70%;
} */
.Cmdb {
  width: 82%;
  height: 18rem;
  margin: 1rem auto;
  padding: 0.2rem 0.5rem;
  position: relative;
  box-shadow: 0.05rem 0.05rem 0.5rem #ccc;
}
.cmdb {
  border-radius: 50%;
  position: absolute;
  width: 4.5rem;
  height: 4.5rem;
  display: flex;
  align-items: center;
}

.cmdb:nth-of-type(1) {
  width: 5.5rem;
  height: 5.5rem;
  background: red;
  background-image: linear-gradient(red, rgb(255, 145, 0));
  left: 33%;
  top: 35%;
}
.cmdb:nth-of-type(2) {
  background: red;
  background-image: linear-gradient(#ff9a0c, #f3e84cde, #ff9a0c);
  left: 10%;
  top: 14%;
}
.cmdb:nth-of-type(3) {
  background: red;
  background-image: linear-gradient(#ff9a0c, #f3e84cde, #ff9a0c);
  left: 65%;
  top: 20%;
}
.cmdb:nth-of-type(4) {
  background: red;
  background-image: linear-gradient(#999696, #c3c1c1, #979494);
  left: 20%;
  top: 70%;
}
.cmdb:nth-of-type(5) {
  background: red;
  background-image: linear-gradient(#ff9a0c, #f3e84cde, #ff9a0c);
  left: 52%;
  top: 68%;
}
.cmdb:nth-of-type(6) {
  background: red;
  background-image: linear-gradient(#999696, #c3c1c1, #979494);
  left: 5%;
  top: 45%;
}
.cmdb:nth-of-type(7) {
  background: red;
  background-image: linear-gradient(#999696, #c3c1c1, #979494);
  left: 40%;
  top: 7%;
}
.cmdb:nth-of-type(8) {
  background: red;
  background-image: linear-gradient(#999696, #c3c1c1, #979494);
  left: 71%;
  top: 46%;
}
.cmdbs {
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 0.3rem;
  position: relative;
}
.line {
  position: absolute;
  width: 2rem;
  height: 2px;
  background: #ff9a0c;
  left: 1rem;
  top: 1rem;
}
.phone {
  color: rgb(77, 71, 71);
  font-weight: 600;
}
.cmdb:nth-of-type(1) .line {
  display: none;
}
.cmdb:nth-of-type(2) .line {
  left: 3.8rem;
  top: 4rem;
  width: 1.2rem;
  transform: rotate(40deg);
}
.cmdb:nth-of-type(3) .line {
  left: -0.2rem;
  top: 4rem;
  width: 1.2rem;
  transform: rotate(-45deg);
}
.cmdb:nth-of-type(4) .line {
  left: 2.6rem;
  top: -0.5rem;
  width: 1.6rem;
  transform: rotate(-60deg);
}
.cmdb:nth-of-type(5) .line {
  left: 0.6rem;
  top: -0.4rem;
  width: 1.4rem;
  transform: rotate(60deg);
}
.cmdb:nth-of-type(6) .line {
  left: 4.4rem;
  top: 1.6rem;
  width: 0.4rem;
  transform: rotate(-12deg);
}
.cmdb:nth-of-type(7) .line {
  left: 1.6rem;
  top: 4.8rem;
  width: 0.7rem;
  transform: rotate(-80deg);
}
.cmdb:nth-of-type(8) .line {
  left: -1rem;
  top: 1.6rem;
  width: 1rem;
  transform: rotate(10deg);
}
#Cmdb2 {
  width: 82%;
  height: 15rem;
  margin: 0 auto;
  padding: 0.2rem 0.5rem;
  box-shadow: 0.05rem 0.05rem 0.5rem #ccc;
}
