  height: 15rem;
  margin: 0 auto;
  padding: 0.2rem 0.5rem;
  box-shadow: 0.05rem 0.05rem 0.5rem #ccc;
  background: rgba(0, 0, 0, 0);
}
.no_data p {
  text-align: center;
  font-size: 2.6rem;
  margin-top: 5rem;
}
</style>


