  height: 5.5rem;
  background: red;
  background-image: linear-gradient(red, rgb(255, 145, 0));
  left: 33%;
  top: 35%;
}
.cmdb:nth-of-type(2) {
  background: red;
  background-image: linear-gradient(#ff9a0c, #f3e84cde, #ff9a0c);
  left: 10%;
  top: 14%;
}
.cmdb:nth-of-type(3) {
  background: red;
  background-image: linear-gradient(#ff9a0c, #f3e84cde, #ff9a0c);
  left: 65%;
  top: 20%;
}
.cmdb:nth-of-type(4) {
