  top: 46%;
}
.cmdbs {
  width: 100%;
  text-align: center;
  color: #fff;
  font-size: 0.3rem;
  position: relative;
}
.line {
  position: absolute;
  width: 2rem;
  height: 2px;
  background: #ff9a0c;
  left: 1rem;
  top: 1rem;
}
.phone {
  color: rgb(77, 71, 71);
  font-weight: 600;
}
.cmdb:nth-of-type(1) .line {
  display: none;
}
.cmdb:nth-of-type(2) .line {
  left: 3.8rem;
  top: 4rem;
  width: 1.2rem;
  transform: rotate(40deg);
}
.cmdb:nth-of-type(3) .line {
  left: -0.2rem;
  top: 4rem;
  width: 1.2rem;
  transform: rotate(-45deg);
}
.cmdb:nth-of-type(4) .line {
  left: 2.6rem;
  top: -0.5rem;
  width: 1.6rem;
  transform: rotate(-60deg);
}
.cmdb:nth-of-type(5) .line {
  left: 0.6rem;
  top: -0.4rem;
  width: 1.4rem;
  transform: rotate(60deg);
}
.cmdb:nth-of-type(6) .line {
