  top: 4.8rem;
  width: 0.7rem;
  transform: rotate(-80deg);
}
.cmdb:nth-of-type(8) .line {
  left: -1rem;
  top: 1.6rem;
  width: 1rem;
  transform: rotate(10deg);
}
#main{
  height: 12rem;
  width: 82%;
  margin: 1rem auto;
  box-shadow: 0.05rem 0.05rem 0.5rem #ccc;
  padding: 0.2rem 0.5rem;
  font-size: 0.7rem;
}
#main canvas{
  width: 100% !important;
}
</style>


